  import { mapState } from 'vuex'
  export default {
  name: 'cmdCompontent',
  components : {  },
  props :{
      
  },
  computed :{
      ...mapState({ cmd : state => state.Cmd })
  },
  data() {
      return {
          contentStyle () {
              return {
                  backgroundColor: 'rgba(0,0,0,0.02)',
                  color: '#555'
              }
          },

          contentActiveStyle () {
              return {
                  backgroundColor: '#eee',
                  color: 'black'
              }
          },

          thumbStyle () {
              return {
                  right: '2px',
                  borderRadius: '5px',
                  backgroundColor: '#027be3',
                  width: '5px',
                  opacity: 0.75
              }
          }
      }
  },
  created :function(){

  },
  methods : {
      onHide :function(){
          this.$store.dispatch('Cmd/status','hide');
      },
      onClose :function(){
          this.$store.dispatch('Cmd/status','close');
      },
      onShow :function(){
          this.$store.dispatch('Cmd/status','show');
      }
  },
};
