import { mapState } from 'vuex'
export default {
  name: 'aux-add-api',
  props :{
      show :{
          type :[Boolean],
          default :false
      }
  },
  data() {
    return {
      pramas:'',
      api :''
    }
  },
  computed: {
    ...mapState({ 
          MProject : state => state.Project
      })
  },
  methods : {
      onReset :function(){
        this.arguments = '';
        this.api = "";
      },
      onOk :function(){
        var  defaultProject = this.MProject.defaultProject;
        var result = {};
        if(defaultProject && this.api){
          result = {
            success :true,
            pramas :this.pramas,
            api :this.api,
            project : defaultProject,
            url : defaultProject.domain + this.api
          }
        }else{
            result = { 
              success :false,
              msg :'当前无项目或API为空'
            }
        }
        this.onReset();
        this.$emit('ok',result)
      },
      onCancel :function(){
        // this.onReset();
        this.$emit('cancel')
      },
      onDialogHide :function(){
        // this.onReset();
        this.$emit('cancel')
      },

  },
};
